.schedule {
  display: flex;

  &__wrapper {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    height: 70vh;
    overflow-x: hidden;
    overflow-y: auto;
    margin-top: clamp(0.813rem, 2.857vw - 1.241rem, 2.188rem);
  }
}